<template>
  <div v-if="getBuyer">
    <b-row>
      <!-- progressbar -->
      <div class="col-md-12">
        <div class="progressbar-container text-center">
          <ul class="progressbar">
            <li class="progressbar-done">檢視購物車</li>
            <li class="progressbar-active">付款方式及寄送資訊</li>
            <li>確認訂單資料</li>
            <li>訂單成立</li>
          </ul>
        </div>
      </div>
      <!-- progressbar -->
    </b-row>

    <b-form @submit="onSubmit">
      <b-row class="mt-3">
        <b-col md="12">
          <b-card
            header="配送方式"
            header-text-variant="dark"
            header-tag="header"
            header-bg-variant="muted"
          >
            <b-row class="d-flex align-items-center justify-content-start">
              <span class="mr-3"></span>
              <input
                type="radio"
                key="homeDelivery"
                :checked="checkDeliveryHome"
                @change="setDelivery($event)"
                name="delivery"
                value="homeDelivery"
              />
              <span class="ml-2 mr-3">宅配</span>
              <input
                type="radio"
                key="postalDelivery"
                :checked="checkDeliveryPost"
                @change="setDelivery($event)"
                name="delivery"
                value="postalDelivery"
              />
              <span class="ml-2">郵寄</span>
            </b-row>
          </b-card>
        </b-col>
      </b-row>

      <b-row class="mt-3">
        <b-col md="12">
          <b-card
            header="付款方式"
            header-text-variant="dark"
            header-tag="header"
            header-bg-variant="muted"
          >
            <b-row class="d-flex align-items-center justify-content-start">
              <span class="mr-3"></span>
              <input
                type="radio"
                key="COD"
                :checked="checkPayment"
                @change="setPayment($event)"
                name="payment"
                value="COD"
              />
              <span class="ml-2">貨到付款</span>
            </b-row>
          </b-card>
        </b-col>
      </b-row>

      <b-row class="mt-3">
        <b-col md="12">
          <b-card
            header="發票資訊"
            header-text-variant="dark"
            header-tag="header"
            header-bg-variant="muted"
          >
            <b-row class="d-flex align-items-center justify-content-start">
              <span class="mr-3"></span>
              <input
                type="radio"
                :checked="checkInvoiceType"
                @change="setInvoiceType($event)"
                name="invoiceType"
                value="paper"
              />
              <span class="ml-2">紙本發票</span>
            </b-row>
          </b-card>
        </b-col>
      </b-row>

      <b-row class="mt-3">
        <b-col md="12">
          <b-card
            header="訂購人資訊"
            header-text-variant="dark"
            header-tag="header"
            header-bg-variant="muted"
          >
            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label for="buyerName">訂購人</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span id="buyerName" v-text="getBuyer.name"></span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label>Email</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span v-text="getBuyer.email"></span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label for="buyerContactNumber">聯絡電話</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span
                  id="buyerContactNumber"
                  v-text="getBuyer.contactNumber"
                ></span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label for="buyerMobile">手機號碼</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span id="buyerMobile" v-text="getBuyer.mobile"></span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label for="buyerAddress">地址</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span id="buyerAddress" v-text="getBuyer.address"></span>
              </b-col>
            </b-row>
          </b-card>
        </b-col>
      </b-row>

      <b-row class="mt-3">
        <b-col md="12">
          <b-card header-tag="header">
            <template v-slot:header>
              <div class="bg-muted text-dark">
                <span class="mr-5">收件人資訊</span>
              </div>
            </template>

            <b-row class="d-flex align-items-center justify-content-start mb-2">
              <span class="mr-3"></span>
              <input
                type="radio"
                key="same"
                :checked="checkReceiverInfoSame"
                @change="setReceiverInfo($event)"
                name="radioBtn"
                value="same"
              />
              <span class="ml-2 mr-3">同訂購人</span>
            </b-row>

            <b-row class="d-flex align-items-center justify-content-start mb-2">
              <span class="mr-3"></span>
              <input
                type="radio"
                key="new"
                :checked="checkReceiverInfoNew"
                @change="setReceiverInfo($event)"
                name="radioBtn"
                value="new"
              />
              <span class="ml-2">修改收件人資訊</span>
            </b-row>

            <b-row class v-show="show">
              <b-col md="2" class="d-flex align-items-center ml-1 my-1">
                <label for="receiver-name">收件人</label>
              </b-col>
              <b-col md="9" class="my-1">
                <b-form-input
                  size="lg"
                  :value="getReceiverName"
                  :state="getReceiverNameState"
                  @input="setReceiverName"
                  @blur="checkReceiverName"
                  id="receiver-name"
                  name="receiver-name"
                  placeholder="請輸入收件人的姓名"
                ></b-form-input>
              </b-col>
            </b-row>

            <b-row class="mt-2" v-show="show">
              <b-col md="2" class="d-flex align-items-center ml-1 my-1">
                <label for="receiver-phone">聯絡電話</label>
              </b-col>
              <b-col lg="2" class="my-1">
                <b-form-input
                  size="lg"
                  :value="getArea"
                  :state="getAreaState"
                  @input="setArea"
                  @blur="checkArea"
                  id="receiver-area"
                  name="receiver-area"
                  placeholder="區域號碼"
                ></b-form-input>
              </b-col>
              <b-col
                lg="5"
                class="d-flex align-items-center justify-content-center my-1"
              >
                <b-form-input
                  size="lg"
                  :value="getPhone"
                  :state="getPhoneState"
                  @input="setPhone"
                  @blur="checkPhone"
                  id="receiver-phone"
                  name="receiver-phone"
                  placeholder="請輸入收件人的聯絡電話"
                ></b-form-input>
              </b-col>
              <b-col lg="2" class="my-1">
                <b-form-input
                  size="lg"
                  :value="getExt"
                  :state="getExtState"
                  @input="setExt"
                  @blur="checkExt"
                  id="receiver-ext"
                  name="receiver-ext"
                  placeholder="分機號碼"
                ></b-form-input>
              </b-col>
            </b-row>

            <b-row class="mt-2" v-show="show">
              <b-col md="2" class="d-flex align-items-center ml-1 my-1">
                <label for="receiver-mobile">手機號碼</label>
              </b-col>
              <b-col md="9" class="mt-1">
                <b-form-input
                  size="lg"
                  :value="getMobile"
                  :state="getMobileState"
                  @input="setMobile"
                  @blur="checkMobile"
                  id="receiver-mobile"
                  name="receiver-mobile"
                  placeholder="請輸入收件人的手機號碼"
                ></b-form-input>
              </b-col>
            </b-row>

            <b-row class="mt-2" v-show="show">
              <b-col md="2" class="d-flex align-items-start ml-1 my-1">
                <label for="receiver-address">地址</label>
              </b-col>
              <b-col md="9" class="my-1">
                <twzipcode
                  :class-names="{
                    county: 'form-control form-control-lg',
                    district: 'form-control form-control-lg',
                    zipcode: 'form-control form-control-lg',
                  }"
                  :default-zipcode="getZipcode"
                  ref="twzipcode"
                  @update="setZipCode"
                ></twzipcode>
                <b-form-input
                  size="lg"
                  :value="getAddress"
                  :state="getAddressState"
                  @input="setAddress"
                  @blur="checkAddress"
                  class="mt-2"
                  id="receiver-address"
                  name="receiver-address"
                  placeholder="請輸入收件人地址"
                ></b-form-input>
              </b-col>
            </b-row>
          </b-card>

          <div class="d-flex justify-content-end mt-3">
            <b-button
              class="mr-3"
              variant="outline-primary"
              @click="goShoppingCart"
            >
              <font-awesome-icon
                :icon="['fas', 'angle-double-left']"
              />&nbsp;上一步
            </b-button>
            <b-button type="submit" class="mr-3" variant="outline-primary">
              下一步&nbsp;
              <font-awesome-icon :icon="['fas', 'angle-double-right']" />
            </b-button>
          </div>
        </b-col>
      </b-row>
    </b-form>
  </div>
</template>
 
<script>
import { mapGetters } from "vuex";
// import buyer from "@/api/buyer.js";
import styles from "@/assets/css/progress-bar.css";
import Twzipcode from "vue2-twzipcode";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faAngleDoubleRight,
  faAngleDoubleLeft,
} from "@fortawesome/free-solid-svg-icons";
import ApiService from "@/common/api.service";
import store from "@/store";
import util from "@/common/util";
import { JWT_EXPIRED_MSG } from "@/common/constant";

library.add(faAngleDoubleRight, faAngleDoubleLeft);

export default {
  name: "Deliver",
  data() {
    return {
      // deliverForm: {
      //   deliver: "postalDelivery",
      //   payment: "COD",
      //   invoiceType: "paper",
      //   receiverInfo: "same",
      //   zipcode: "",
      //   county: "",
      //   district: "",
      //   receiverName: "",
      //   receiverArea: "",
      //   receiverPhone: "",
      //   receiverExt: "",
      //   receiverMobile: "",
      //   receiverAddress: "",
      // },
      // buyer: null,
      errors: [],
    };
  },
  components: {
    Twzipcode,
  },
  // https://vuejs.org/v2/guide/computed.html#Computed-Caching-vs-Methods
  // https://cn.vuejs.org/v2/guide/computed.html#%E8%AE%A1%E7%AE%97%E5%B1%9E%E6%80%A7-vs-Methods
  computed: {
    show: function () {
      console.log("show");
      // return this.$store.getters["deliver/getState"].receiverInfo === "new";
      // console.log("this.getState()", JSON.stringify(this.getState()));
      return this.getState().receiverInfo === "new";
    },
    deliver() {
      // return this.$store.getters["deliver/getState"].deliver;
      return this.getState().deliver;
    },
    checkDeliveryHome() {
      // return this.$store.getters["deliver/getState"].deliver === "homeDelivery";
      return this.getState().deliver === "homeDelivery";
    },
    checkDeliveryPost() {
      return (
        // this.$store.getters["deliver/getState"].deliver === "postalDelivery"
        this.getState().deliver === "postalDelivery"
      );
    },
    checkPayment() {
      // return this.$store.getters["deliver/getState"].payment === "COD";
      return this.getState().payment === "COD";
    },
    checkInvoiceType() {
      // return this.$store.getters["deliver/getState"].invoiceType === "paper";
      return this.getState().invoiceType === "paper";
    },
    checkReceiverInfoSame() {
      console.log(
        // this.$store.getters["deliver/getState"].receiverInfo === "same"
        "checkReceiverInfoSame",
        this.getState().receiverInfo === "same"
      );
      // return this.$store.getters["deliver/getState"].receiverInfo === "same";
      return this.getState().receiverInfo === "same";
    },
    checkReceiverInfoNew() {
      /*
      console.log(
        // this.$store.getters["deliver/getState"].receiverInfo === "new"
        "checkReceiverInfoNew", this.getState().receiverInfo === "new"
      );
      // return this.$store.getters["deliver/getState"].receiverInfo === "new";
      return this.getState().receiverInfo === "new";
      */
      return this.isReceiverInfoNew();
    },
    getZipcode() {
      // return this.$store.getters["deliver/getState"].zipcode;
      return this.getState().zipcode;
    },
    getReceiverName() {
      // return this.$store.getters["deliver/getState"].receiverName;
      return this.getState().receiverName;
    },
    getReceiverNameState() {
      // return this.$store.getters["deliver/getState"].receiverNameState;
      return this.getState().receiverNameState;
    },
    getArea() {
      console.log("getArea");
      // return this.$store.getters["deliver/getState"].receiverArea;
      return this.getState().receiverArea;
    },
    getAreaState() {
      console.log("getAreaState");
      // return this.$store.getters["deliver/getState"].receiverAreaState;
      return this.getState().receiverAreaState;
    },
    getPhone() {
      // return this.$store.getters["deliver/getState"].receiverPhone;
      return this.getState().receiverPhone;
    },
    getPhoneState() {
      // return this.$store.getters["deliver/getState"].receiverPhoneState;
      return this.getState().receiverPhoneState;
    },
    getExt() {
      // return this.$store.getters["deliver/getState"].receiverExt;
      return this.getState().receiverExt;
    },
    getExtState() {
      // return this.$store.getters["deliver/getState"].receiverExtState;
      return this.getState().receiverExtState;
    },
    getMobile() {
      // return this.$store.getters["deliver/getState"].receiverMobile;
      return this.getState().receiverMobile;
    },
    getMobileState() {
      // return this.$store.getters["deliver/getState"].receiverMobileState;
      return this.getState().receiverMobileState;
    },
    getAddress() {
      // return this.$store.getters["deliver/getState"].receiverAddress;
      return this.getState().receiverAddress;
    },
    getAddressState() {
      // return this.$store.getters["deliver/getState"].receiverAddressState;
      return this.getState().receiverAddressState;
    },
    ...mapGetters("auth", ["getBuyer"]),
  },
  methods: {
    ...mapGetters("deliver", ["getState"]),
    dispatchProperty(key, value) {
      this.$store.dispatch("deliver/setProperty", {
        key,
        value,
      });
    },
    setDelivery(event) {
      console.log("deliver", event.target.value);
      this.dispatchProperty("deliver", event.target.value);
    },
    setPayment(event) {
      this.dispatchProperty("payment", event.target.value);
    },
    setInvoiceType(event) {
      this.dispatchProperty("invoiceType", event.target.value);
    },
    setReceiverInfo(event) {
      this.dispatchProperty("receiverInfo", event.target.value);
    },
    setReceiverName(value) {
      console.log("setReceiverName", value);
      this.dispatchProperty("receiverName", value);
    },
    setArea(value) {
      console.log("setArea", value);
      this.dispatchProperty("receiverArea", value);
    },
    setPhone(value) {
      console.log("receiverPhone", value);
      this.dispatchProperty("receiverPhone", value);
    },
    setExt(value) {
      console.log("receiverExt", value);
      this.dispatchProperty("receiverExt", value);
    },
    setMobile(value) {
      console.log("receiverExt", value);
      this.dispatchProperty("receiverMobile", value);
    },
    setAddress(value) {
      console.log("receiverAddress", value);
      this.dispatchProperty("receiverAddress", value);
    },
    fetchData() {
      this.buyer = buyer;
    },
    goShoppingCart() {
      this.$router.push({ name: "shoppingcart" });
    },
    goConfirm() {
      this.$router.push({ name: "confirm" });
    },
    setZipCode({ zipcode, county, district }) {
      console.log("zipcode", zipcode);
      this.dispatchProperty("zipcode", zipcode);
      console.log("county", county);
      this.dispatchProperty("county", county);
      console.log("district", district);
      this.dispatchProperty("district", district);
    },
    isReceiverInfoNew() {
      console.log(
        // this.$store.getters["deliver/getState"].receiverInfo === "new"
        "isReceiverInfoNew",
        this.getState().receiverInfo === "new"
      );
      // return this.$store.getters["deliver/getState"].receiverInfo === "new";
      return this.getState().receiverInfo === "new";
    },
    onSubmit(event) {
      event.preventDefault();
      this.errors.splice(0, this.errors.length);
      // let state = this.getState();
      // let state = this.$store.getters["deliver/getState"];
      // if (state.receiverInfo === "new") {
      //   this.validateData(state);
      // }

      if (this.isReceiverInfoNew()) {
        this.validateData(this.getState());
      }
      if (this.errors.length) {
        this.showErrors();
      } else {
        this.$store.dispatch("deliver/setState");
        this.goConfirm();
      }
    },
    validateData(state) {
      this.validateReceiverName(state.receiverName, true);
      this.validateArea(state.receiverArea, true);
      this.validatePhone(state.receiverPhone, true);
      this.validateExt(state.receiverExt, true);
      this.validateMobile(state.receiverMobile, true);
      this.validateAddress(state.receiverAddress, true);
    },
    showErrors() {
      this.errors.forEach((ele) => {
        this.$toastr.e(ele);
      });
    },
    // https://bootstrap-vue.org/docs/components/form-input#comp-ref-b-form-input-props
    // state
    // Boolean	null	Controls the validation state appearance of the component. `true` for valid, `false` for invalid, or `null` for no validation state
    validation(contextualState, regex, value, errMsg, isSubmitted) {
      let ret = regex.test(value);
      if (!ret) {
        if (isSubmitted) {
          this.errors.push(errMsg);
        } else {
          this.$toastr.e(errMsg);
        }
        this.dispatchProperty(contextualState, false);
      } else {
        this.dispatchProperty(contextualState, null);
      }
    },
    // validation(property, regex, value, errMsg) {
    //   let ret = regex.test(value);
    //   if (!ret) {
    //     this.errors.push(errMsg);
    //     this.$toastr.e(errMsg);
    //     this.errors.length = 0;
    //     this.dispatchProperty(property, false);
    //   } else {
    //     this.dispatchProperty(property, null);
    //   }
    // },
    isNotEmpty(contextualState, value, errMsg, isSubmitted) {
      // https://stackoverflow.com/questions/3085539/regular-expression-for-anything-but-an-empty-string
      // match any string that contains at least one non-space character
      this.validation(
        contextualState,
        /^(?!\s*$).+/,
        value,
        errMsg,
        isSubmitted
      );
    },
    checkReceiverName(event) {
      console.log(
        "checkReceiverName",
        event.target.value,
        event.target.value.length
      );
      if (this.isReceiverInfoNew()) {
        // this.isNotEmpty("receiverNameState", event.target.value, "請輸入收件人姓名", false);
        this.validateReceiverName(event.target.value, false);
      }
    },
    validateReceiverName(value, isSubmitted) {
      this.isNotEmpty(
        "receiverNameState",
        value,
        "請輸入收件人姓名",
        isSubmitted
      );
    },
    checkArea(event) {
      console.log("checkArea");
      if (this.isReceiverInfoNew()) {
        this.validateArea(event.target.value, false);
      }
    },
    validateArea(value, isSubmitted) {
      // 0開頭 2-9出現1到3次 或 空格
      // let regex = /^[0][2-9]{1,3}$|^$/;
      // 0開頭 2-9出現1到3次
      let regex = /^[0][2-9]{1,3}$/;
      // if(!regex.test(value)) {
      //   this.errors.push("請輸入正確的區域號碼");
      // }
      console.log("validateArea", value, regex.test(value));
      this.validation(
        "receiverAreaState",
        regex,
        value,
        "請輸入正確的區域號碼",
        isSubmitted
      );
    },
    checkPhone(event) {
      if (this.isReceiverInfoNew()) {
        this.validatePhone(event.target.value, false);
      }
    },
    validatePhone(value, isSubmitted) {
      // 數字7到8次 或 空格
      // let regex = /^\d{7,8}$|^$/;
      // 數字7到8次
      let regex = /^\d{7,8}$/;
      // if(!regex.test(value)) {
      //   this.errors.push("請輸入正確的聯絡電話");
      // }
      console.log("validatePhone", value, regex.test(value));
      this.validation(
        "receiverPhoneState",
        regex,
        value,
        "請輸入正確的聯絡電話",
        isSubmitted
      );
    },
    checkExt(event) {
      if (this.isReceiverInfoNew()) {
        this.validateExt(event.target.value, false);
      }
    },
    validateExt(value, isSubmitted) {
      // 數字1到8次 或 空格
      let regex = /^\d{1,8}$|^$/;

      // if(!regex.test(value)) {
      //   this.errors.push("請輸入正確的分機號碼");
      // }
      console.log("validateExt", value, regex.test(value));
      this.validation(
        "receiverExtState",
        regex,
        value,
        "請輸入正確的分機號碼",
        isSubmitted
      );
    },
    checkMobile(event) {
      if (this.isReceiverInfoNew()) {
        this.validateMobile(event.target.value, false);
      }
    },
    validateMobile(value, isSubmitted) {
      // 09開頭 數字出現8次 或 空格
      // let regex = /^[0][9]\d{8}$|^$/;
      // 09開頭 數字出現8次
      let regex = /^[0][9]\d{8}$/;
      // if(!regex.test(value)) {
      //   this.errors.push("請輸入正確的手機號碼");
      // }
      console.log("validateMobile", event.target.value, regex.test(value));
      this.validation(
        "receiverMobileState",
        regex,
        value,
        "請輸入正確的手機號碼",
        isSubmitted
      );
    },
    checkAddress(event) {
      if (this.isReceiverInfoNew()) {
        this.validateAddress(event.target.value, false);
      }
    },
    validateAddress(value, isSubmitted) {
      this.isNotEmpty(
        "receiverAddressState",
        value,
        "請輸入收件人地址",
        isSubmitted
      );
    },
  },
  // Uncaught (in promise) Error: Redirected when going from "/shoppingcart" to "/deliver" via a navigation guard.
  // beforeRouteEnter(to, from, next) {
  //   console.log("Deliver.vue beforeRouteEnter");
  //   ApiService.setHeader();
  //   ApiService.get("user/info")
